import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {RootState} from "./state";

// Define the initial slice state
const initialState: PoolState = {
    pools: null
}

// Define the slice
const poolSlice = createSlice({
    name: 'pool',
    initialState,
    reducers: {
        setPools(state, action: PayloadAction<PoolState['pools'] | null>) {
            // @todo allow for pagination
            state.pools = action.payload
        },
    },
})

// Define getters
export const getPools = (state: RootState): PoolState['pools'] => {
    return state.pool.pools
}

// Define mutators
export const { setPools } = poolSlice.actions

// Export the slice
export default poolSlice.reducer
